"use client"

import type React from "react"
import { useState, useRef, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Send, Music, User, Search } from "lucide-react"
import { getStudentsWithLastMessage, getMessagesByStudentId, type Message, type Student } from "@/lib/mock-data"

export function AdminChat() {
  const studentsWithMessages = getStudentsWithLastMessage()
  const [selectedStudent, setSelectedStudent] = useState<
    (Student & { lastMessage?: Message; unreadCount: number }) | null
  >(null) // Start with no student selected so sidebar is more prominent
  const [messages, setMessages] = useState<Message[]>([])
  const [newMessage, setNewMessage] = useState("")
  const [searchQuery, setSearchQuery] = useState("")
  const messagesEndRef = useRef<HTMLDivElement>(null)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  useEffect(() => {
    if (selectedStudent) {
      setMessages(getMessagesByStudentId(selectedStudent.id))
    }
  }, [selectedStudent])

  const handleSelectStudent = (student: typeof selectedStudent) => {
    setSelectedStudent(student)
  }

  const handleSendMessage = () => {
    if (!newMessage.trim() || !selectedStudent) return

    const message: Message = {
      id: `msg-${Date.now()}`,
      student_id: selectedStudent.id,
      sender: "teacher",
      content: newMessage.trim(),
      timestamp: new Date().toISOString(),
      read: true,
    }

    setMessages((prev) => [...prev, message])
    setNewMessage("")
  }

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault()
      handleSendMessage()
    }
  }

  const formatTimestamp = (timestamp: string) => {
    const date = new Date(timestamp)
    const now = new Date()
    const diffDays = Math.floor((now.getTime() - date.getTime()) / (1000 * 60 * 60 * 24))

    if (diffDays === 0) {
      return date.toLocaleTimeString("en-US", { hour: "numeric", minute: "2-digit" })
    } else if (diffDays === 1) {
      return "Yesterday"
    } else if (diffDays < 7) {
      return date.toLocaleDateString("en-US", { weekday: "short" })
    } else {
      return date.toLocaleDateString("en-US", { month: "short", day: "numeric" })
    }
  }

  const filteredStudents = studentsWithMessages.filter((student) =>
    student.name.toLowerCase().includes(searchQuery.toLowerCase()),
  )

  const totalUnread = studentsWithMessages.reduce((acc, s) => acc + s.unreadCount, 0)

  return (
    <Card className="h-[600px] overflow-hidden">
      <div className="grid grid-cols-[320px_1fr] h-full">
        {/* Student Sidebar - Always Visible */}
        <div className="border-r flex flex-col bg-muted/30 h-full">
          <div className="p-4 border-b">
            <div className="flex items-center justify-between mb-3">
              <h3 className="font-serif font-semibold text-lg">Students</h3>
              {totalUnread > 0 && <Badge variant="destructive">{totalUnread} new</Badge>}
            </div>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search students..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
          </div>

          <ScrollArea className="flex-1">
            <div className="p-2">
              {filteredStudents.map((student) => (
                <button
                  key={student.id}
                  onClick={() => handleSelectStudent(student)}
                  className={`w-full p-3 rounded-lg text-left transition-colors mb-1 ${
                    selectedStudent?.id === student.id ? "bg-primary text-primary-foreground" : "hover:bg-muted"
                  }`}
                >
                  <div className="flex items-start gap-3">
                    <div
                      className={`h-10 w-10 rounded-full flex items-center justify-center shrink-0 ${
                        selectedStudent?.id === student.id ? "bg-primary-foreground/20" : "bg-primary/10"
                      }`}
                    >
                      <User
                        className={`h-5 w-5 ${
                          selectedStudent?.id === student.id ? "text-primary-foreground" : "text-primary"
                        }`}
                      />
                    </div>
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center justify-between gap-2">
                        <span className="font-medium truncate">{student.name}</span>
                        {student.unreadCount > 0 && selectedStudent?.id !== student.id && (
                          <Badge
                            variant="destructive"
                            className="h-5 w-5 p-0 flex items-center justify-center text-xs shrink-0"
                          >
                            {student.unreadCount}
                          </Badge>
                        )}
                      </div>
                      {student.lastMessage && (
                        <div className="flex items-center justify-between gap-2 mt-0.5">
                          <p
                            className={`text-xs truncate ${
                              selectedStudent?.id === student.id
                                ? "text-primary-foreground/70"
                                : "text-muted-foreground"
                            }`}
                          >
                            {student.lastMessage.sender === "teacher" ? "You: " : ""}
                            {student.lastMessage.content}
                          </p>
                          <span
                            className={`text-[10px] shrink-0 ${
                              selectedStudent?.id === student.id
                                ? "text-primary-foreground/60"
                                : "text-muted-foreground"
                            }`}
                          >
                            {formatTimestamp(student.lastMessage.timestamp)}
                          </span>
                        </div>
                      )}
                    </div>
                  </div>
                </button>
              ))}
            </div>
          </ScrollArea>
        </div>

        {/* Main Chat Window */}
        <div className="flex flex-col h-full overflow-hidden">
          {selectedStudent ? (
            <>
              {/* Chat Header */}
              <div className="p-4 border-b flex items-center gap-3 shrink-0">
                <div className="h-10 w-10 bg-primary/10 rounded-full flex items-center justify-center">
                  <User className="h-5 w-5 text-primary" />
                </div>
                <div className="flex-1">
                  <h3 className="font-serif font-semibold">{selectedStudent.name}</h3>
                  <p className="text-xs text-muted-foreground">
                    {selectedStudent.email}  {selectedStudent.credits_remaining} credits
                  </p>
                </div>
                {selectedStudent.balance_due > 0 && (
                  <Badge variant="destructive">${selectedStudent.balance_due} due</Badge>
                )}
              </div>

              {/* Messages */}
              <ScrollArea className="flex-1 p-4">
                <div className="space-y-3">
                  {messages.length === 0 ? (
                    <div className="flex flex-col items-center justify-center h-full text-center py-12">
                      <Music className="h-10 w-10 text-muted-foreground mb-3" />
                      <p className="text-muted-foreground">No messages yet</p>
                      <p className="text-sm text-muted-foreground">Start a conversation with {selectedStudent.name}</p>
                    </div>
                  ) : (
                    messages.map((message) => (
                      <div
                        key={message.id}
                        className={`flex ${message.sender === "teacher" ? "justify-end" : "justify-start"}`}
                      >
                        <div
                          className={`max-w-[70%] rounded-2xl px-4 py-2.5 ${
                            message.sender === "teacher"
                              ? "bg-primary text-primary-foreground rounded-br-sm"
                              : "bg-muted rounded-bl-sm"
                          }`}
                        >
                          <p className="text-sm leading-relaxed">{message.content}</p>
                          <p
                            className={`text-[10px] mt-1 ${
                              message.sender === "teacher" ? "text-primary-foreground/60" : "text-muted-foreground"
                            }`}
                          >
                            {formatTimestamp(message.timestamp)}
                          </p>
                        </div>
                      </div>
                    ))
                  )}
                  <div ref={messagesEndRef} />
                </div>
              </ScrollArea>

              {/* Input */}
              <div className="border-t p-4 shrink-0">
                <div className="flex gap-2">
                  <Input
                    placeholder={`Message ${selectedStudent.name}...`}
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    onKeyDown={handleKeyDown}
                    className="flex-1"
                  />
                  <Button onClick={handleSendMessage} disabled={!newMessage.trim()}>
                    <Send className="h-4 w-4 mr-2" />
                    Send
                  </Button>
                </div>
              </div>
            </>
          ) : (
            <div className="flex-1 flex flex-col items-center justify-center text-center p-8 bg-muted/10">
              <div className="h-20 w-20 rounded-full bg-primary/10 flex items-center justify-center mb-6">
                <Music className="h-10 w-10 text-primary" />
              </div>
              <h3 className="font-serif text-2xl font-semibold mb-2">Select a Conversation</h3>
              <p className="text-muted-foreground max-w-sm">
                Choose a student from the list on the left to view their message history and send replies
              </p>
            </div>
          )}
        </div>
      </div>
    </Card>
  )
}
